body {
  margin: 0;
  background-color: #ede3d9;
  background-image: url(../img/bg-body.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
img {
  max-width: 100%;
}
.container {
  overflow: hidden;
}
.block {
  display: block;
}
.price {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0;
  line-height: 1.6;
  font-family: "Roboto Slab", serif;
  text-transform: uppercase;
}
.price del {
  margin: 0 5px;
  font-size: 14px;
  color: #782b07;
  text-decoration-line: line-through;
}
.price span {
  margin: 0 5px;
  font-weight: bold;
  font-size: 20px;
  color: #742501;
}
@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1);
  }
  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}
.header {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  padding-left: 15px;
  padding-right: 15px;
}
@media (min-width: 500px) {
  .header {
    padding-bottom: 35px;
  }
}
.header__title {
  margin: 0;
  font-weight: 700;
  font-size: 19px;
  line-height: 1.4;
  font-family: "Maitree", serif;
  color: #915110;
  font-style: italic;
}
@media (min-width: 990px) {
  .header__title {
    font-size: 34px;
  }
}
.header__logo {
  display: block;
  margin: 0 auto;
  width: 140px;
}
@media (min-width: 990px) {
  .header__logo {
    width: 265px;
  }
}
.first {
  padding-right: 20px;
  padding-bottom: 50px;
  padding-left: 20px;
}
.first__title {
  max-width: 380px;
  margin: 0 auto 14px;
  padding: 20px 15px;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.2;
  font-family: "Maitree", serif;
  text-align: center;
  color: #ffffff;
  font-style: italic;
  background: #fff601;
  background: linear-gradient(180deg, #fff601, #c76f16);
  border-width: 2px;
  border-style: dashed;
  border-color: #ffffff;
  border-radius: 214px;
  transform: rotate(-10deg);
  text-decoration-line: underline;
}
@media (min-width: 990px) {
  .first__title {
    max-width: fit-content;
  }
}
.first__block-img {
  position: relative;
}
.first__block-img::before {
  content: '';
  position: absolute;
  bottom: 18px;
  left: -10px;
  width: 104px;
  height: 115px;
  background-image: url(../img/vitamin-e.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: scale(-1, 1);
}
@media (min-width: 990px) {
  .first__block-img::before {
    width: 181px;
    height: 181px;
    background-image: url(../img/natural-label.png);
  }
}
.first__block-img::after {
  content: "";
  position: absolute;
  right: -98px;
  bottom: -139px;
  z-index: -1;
  width: 198px;
  height: 324px;
  background-image: url(../img/leaf.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: rotate(-135deg);
}
@media (min-width: 990px) {
  .first__block-img::after {
    width: 493px;
    height: 572px;
    z-index: -2;
  }
}
.first__img-1 {
  display: block;
  box-sizing: border-box;
  width: 170px;
  margin: 0 auto;
  border: 4px solid #fff;
  transform: rotate(-3deg);
}
@media (min-width: 990px) {
  .first__img-1 {
    position: relative;
    z-index: -1;
    width: 365px;
    border-width: 7px;
  }
}
.first__img-2 {
  position: absolute;
  top: -5px;
  left: 50%;
  width: 98px;
  border: 4px solid #fff;
  transform: translateX(50%);
}
@media (min-width: 990px) {
  .first__img-2 {
    width: 147px;
    border-width: 5px;
  }
}
.first-list {
  width: fit-content;
  margin: 0 auto 70px;
  padding-right: 15px;
  padding-left: 70px;
  list-style-type: none;
}
.first-item {
  position: relative;
  display: flex;
  margin-bottom: 10px;
  padding: 5px 15px 0;
  font-weight: bold;
  font-size: 11px;
  line-height: 1.6;
  font-family: "Maitree", serif;
  text-align: left;
  color: #915110;
  font-style: italic;
  background: #fdfbfb;
  background: linear-gradient(180deg, #fdfbfb, #eaedee);
  border-radius: 0px 94px 94px;
}
.first-item::before {
  content: '';
  display: block;
  width: 25px;
  height: 25px;
  margin-top: -5px;
  margin-right: 10px;
  background-position: center;
  border-width: 2px;
  border-style: solid;
  border-color: #e4e4e4;
  border-radius: 50%;
}
.first-item.one::before {
  background-image: url(../img/first-item-one.png);
}
.first-item.two::before {
  background-image: url(../img/first-item-two.png);
}
.first-item.three::after {
  content: '';
  position: absolute;
  top: 7px;
  left: 22px;
  width: 15px;
  height: 15px;
  background-color: #be0027;
  border-radius: 50%;
}
@media (min-width: 990px) {
  .first-section {
    display: flex;
    flex-direction: column;
  }
  .first-list {
    display: flex;
    flex-wrap: wrap;
    order: 1;
    padding-left: 15px;
  }
  .first-item {
    align-items: center;
    margin: 0 7px;
    padding-top: 3px;
    padding-bottom: 3px;
    font-size: 16px;
  }
  .first-item::before {
    width: 39px;
    height: 39px;
    margin-top: -3px;
  }
  .first-item.three::after {
    top: 11px;
    left: 25px;
    width: 22px;
    height: 22px;
  }
  .first {
    order: 2;
  }
  .listing {
    order: 3;
  }
}
.listing__list {
  position: relative;
  width: fit-content;
  margin: 0 auto;
  padding-left: 40px;
  list-style-type: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.6;
  font-family: "Maitree";
  color: #915110;
  font-style: italic;
}
@media (min-width: 990px) {
  .listing__list {
    font-size: 18px;
  }
}
.listing__list:not(:last-child) {
  margin-bottom: 20px;
}
.listing__list:first-child::before {
  content: '';
  position: absolute;
  bottom: -47px;
  left: -20px;
  display: block;
  width: 89px;
  height: 75px;
  background-image: url(../img/green-arrow.svg);
}
@media (min-width: 500px) {
  .listing__list:first-child::before {
    left: -50px;
  }
}
@media (min-width: 990px) {
  .listing__list:first-child::before {
    width: 91px;
    height: 91px;
  }
}
.listing__list:nth-child(2) {
  color: #db5201;
}
.listing__wrap {
  position: relative;
  padding-top: 70px;
}
@media (max-width: 989px) {
  .listing__wrap::before {
    content: '';
    position: absolute;
    top: 0;
    right: 10px;
    width: 100px;
    height: 100px;
    background-image: url(../img/natural-label.png);
    background-size: contain;
  }
  .listing__wrap::after {
    content: '';
    position: absolute;
    top: 97px;
    left: 10px;
    width: 80px;
    height: 80px;
    background-image: url(../img/office-product-label.png);
    background-size: contain;
  }
}
@media (min-width: 990px) {
  .listing__wrap {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
  }
}
.listing__wrap img {
  display: block;
  margin: 0 auto;
}
.volume {
  width: fit-content;
  margin: 0 auto 29px;
}
.volume p {
  position: relative;
  width: fit-content;
  padding: 10px 10px;
  font-size: 13px;
  line-height: 1.6;
  font-family: "Open Sans", sans-serif;
  color: #000000;
  background-color: rgba(255, 255, 255, 0.86);
}
@media (min-width: 990px) {
  .volume p {
    font-size: 17px;
  }
}
.volume p:not(:last-child) {
  margin-bottom: 18px;
}
.volume p::before {
  content: '';
  position: absolute;
  bottom: -7px;
  left: -7px;
  z-index: -1;
  width: calc(100% + 7px);
  height: calc(100% + 7px);
  background-color: rgba(145, 81, 16, 0.35);
}
@media (min-width: 990px) {
  .listing__wrap img,
  .volume {
    margin-right: 0;
    margin-left: 0;
  }
  .volume {
    position: relative;
  }
  .volume::before {
    content: '';
    position: absolute;
    top: -94px;
    right: -72px;
    width: 119px;
    height: 119px;
    background-image: url(../img/office-product-label.png);
  }
}
.form {
  padding: 15px;
  padding-bottom: 50px;
}
.form__discount {
  position: relative;
  box-sizing: border-box;
  max-width: 180px;
  margin: 0 auto 50px;
  padding: 12px 10px 25px;
  font-weight: bold;
  font-size: 17px;
  line-height: 1.2;
  font-family: "Maitree", sans-serif;
  text-align: center;
  color: #ff0e01;
  font-style: italic;
  background-color: #ffffff;
  border-width: 3px;
  border-style: solid;
  border-color: #db5201;
  border-radius: 0px 28px 28px;
}
@media (min-width: 990px) {
  .form__discount {
    max-width: 190px;
    font-size: 18px;
  }
  .form__discount::after {
    width: 150px;
    height: 67px;
  }
}
.form__discount p {
  margin: 0;
}
.form__discount::after {
  content: '';
  display: block;
  width: 140px;
  height: 55px;
  margin: 0 auto;
  background-image: url(../img/discount.gif);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.form__discount::before {
  content: '';
  position: absolute;
  bottom: -65px;
  left: 50%;
  display: block;
  width: 95px;
  height: 95px;
  margin: 0 auto;
  background-image: url(../img/down-arrow.gif);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transform: translateX(-50%);
}
.form__spin {
  position: relative;
  width: 290px;
  margin: 0 auto;
  padding-top: 15px;
}
@media (min-width: 990px) {
  .form__spin {
    width: 407px;
  }
}
.form__spin-button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  background-color: transparent;
  background-image: url(../img/spin-btn1.svg);
  background-repeat: no-repeat;
  background-size: contain;
  border: 0;
  transform: translate(-50%, -50%);
  appearance: none;
}
[class*="form__spin-text-"] {
  position: absolute;
  box-sizing: border-box;
  width: 142px;
  height: 71px;
  padding-left: 12%;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.8;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  color: #fff;
}
.form__spin-text-1 {
  top: -7px;
  left: 10px;
  transform: rotate(-90deg);
  transform-origin: right;
}
.form__spin-text-2 {
  top: 134px;
  left: 142px;
  transform: rotate(-30deg);
  transform-origin: left;
}
.form__spin-text-3 {
  top: 126px;
  left: 137px;
  transform: rotate(-330deg);
  transform-origin: left;
}
.form__spin-text-4 {
  top: 112px;
  left: 137px;
  transform: rotate(-270deg);
  transform-origin: left;
}
.form__spin-text-5 {
  top: 117px;
  left: 150px;
  transform: rotate(-210deg);
  transform-origin: left;
}
.form__spin-text-6 {
  top: 126px;
  left: 157px;
  transform: rotate(-150deg);
  transform-origin: left;
}
.form__content {
  position: relative;
  padding: 10px 25px 20px;
  text-align: center;
  background-color: #ffebd9;
  border-width: 1px;
  border-style: solid;
  border-color: #ffc087;
  border-radius: 15px;
}
.form__content::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: -1;
  width: calc(100% + 12px);
  height: calc(100% + 12px);
  background-color: #ffebd9;
  border-width: 1px;
  border-style: solid;
  border-color: #ffffff;
  border-radius: 20px;
  transform: translate(-50%, -50%);
  opacity: 0.44;
}
.form__title {
  margin: 0 0 20px;
  font-weight: bold;
  font-size: 20px;
  line-height: 1.2;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  color: #b90f0f;
}
.form__content input,
.form__content select {
  display: block;
  box-sizing: border-box;
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
  margin-bottom: 10px;
  padding: 12px 18px;
  font-family: "Open Sans", sans-serif;
  background-color: #fff;
  border-width: 1px;
  border-style: solid;
  border-color: #b74615;
  border-radius: 109px;
  appearance: none;
}
.form__button {
  display: inline-block;
  width: 100%;
  max-width: 350px;
  margin-bottom: 10px;
  padding: 4px 8px;
  overflow: hidden;
  font-weight: 500;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.6;
  font-family: 'Roboto', sans-serif;
  color: #ffffff;
  background-color: #ff0f7a;
  background-image: linear-gradient(180deg, #ff0f7a, #ff0141);
  border-width: 2px;
  border-style: solid;
  border-color: #e6855b;
  border-radius: 130px;
  animation-name: pulse;
  animation-duration: 1s;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  appearance: none;
}
.form__notes {
  margin: 0;
  padding: 0 14px;
  font-size: 12px;
  line-height: 1.2;
  text-align: center;
}
.timer {
  margin-bottom: 10px;
}
.timer__title {
  margin: 0 0 5px;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.6;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  color: #742501;
}
.timer__clock span {
  display: inline-block;
  min-width: 30px;
  padding: 1px 3px;
  font-weight: 700;
  font-size: 22px;
  font-family: 'Barlow', sans-serif;
  color: #ffffff;
  background-color: #da8a66;
  border-width: 1px;
  border-style: solid;
  border-color: #782b07;
  border-radius: 10px;
}
